<style lang="less">

</style>
<template>
	<Form ref="formCustom" :model="formCustom" :rules="ruleCustom" :label-width="80">
		<FormItem label="分类名称" prop="name">
			<Input type="text" v-model="formCustom.name"></Input>
		</FormItem>
		<FormItem label="分类介绍" prop="desc">
			<Input type="text" v-model="formCustom.desc"></Input>
		</FormItem>
		<FormItem>
			<Button type="primary" @click="clickAdd('formCustom')">添加</Button>
			<Button @click="handleReset('formCustom')" style="margin-left: 8px">重置4444</Button>
		</FormItem>
	</Form>
</template>
<script>
import fetch from "../api/fetch"
export default {
	data() {
		return {
			formCustom: { name: "", desc: "" },
			ruleCustom: {
				name: [
					{ required: true, message: "名称不能为空", trigger: "blur" }
				]
			}
		};
	},
	methods: {
        // 添加
		clickAdd(name) {
			this.$refs[name].validate(valid => {
				if (valid) {
					
                    // 把数据 发到服务器
                    fetch.post("/adm/class/add",this.formCustom).then(resp=>{
                        console.log(resp)
                        this.$Message.success("添加成功");
                    })
				} 
			});
		},
		handleReset(name) {
			this.$refs[name].resetFields();
		}
	}
};
</script>